import { Api } from '../base/api';
export class ApiModel extends Api {
    constructor(contentDeliveryUrl, apiBaseUrl, options) {
        super(apiBaseUrl, options);
        this.contentDeliveryUrl = contentDeliveryUrl;
    }
    fetchProductList() {
        console.log(this);
        return this.get('/product').then((data) => data.items.map((product) => (Object.assign(Object.assign({}, product), { imageUrl: this.contentDeliveryUrl + product.image }))));
    }
    submitOrder(order) {
        return this.post('/order', order).then((data) => data);
    }
}
